class AxisActionFactors(val dimensions: Dimensions, axis: Int) {
    val multipliers = dimensions.multipliers()
    val multiplierAxis = multipliers[axis]
    val highValFactor = multiplierAxis * dimensions[axis]

    inline fun <T> withFactors(p: Int, fn: (high: Int, low: Int, axisCoord: Int) -> T): T {
        // The calculation is broken down into its constituents in order to
        // ensure that the generated bytecode performs the operations in the correct order.
        // When a modulo operation is performed together with a division using the same
        // arguments, the Java compiler is able to combine the two operations into
        // a single divide.
        val f = highValFactor
        val m = multiplierAxis
        val lowVal = p % m
        val highVal = p / f
        val r = p % f
        val axisCoord = r / m
        return fn(highVal, lowVal, axisCoord)
    }

    fun indexForAxis(high: Int, low: Int, axisPosition: Int): Int {
        return (highValFactor * high) + (axisPosition * multiplierAxis) + low
    }
}

object IotaArrayImpls {
    interface GenericIotaArrayLong {
        fun resizeIotaArray(d: Dimensions, updatedOffset: Long): APLValue
    }

    class IotaArray(val indexes: IntArray) : APLArray() {
        override val dimensions = Dimensions(indexes)

        private val multipliers = dimensions.multipliers()

        init {
            require(indexes.isNotEmpty()) { "indexes is empty" }
        }

        override fun valueAt(p: Int): APLValue {
            val index = multipliers.positionFromIndex(p)
            return APLArrayLong(dimensionsOfSize(indexes.size), LongArray(index.size) { i -> index[i].toLong() })
        }
    }

    class IotaArrayLong(val length: Int) : APLArray(), GenericIotaArrayLong {
        override val dimensions = dimensionsOfSize(length)
        override val specialisedType get() = ArrayMemberType.LONG
        override fun collapseInt(withDiscard: Boolean) = this

        override fun valueAtCoerceToInt(p: Int, pos: Position?): Int {
            if (p < 0 || p >= length) {
                throwAPLException(APLIndexOutOfBoundsException("Position in array: ${p}, size: ${length}", pos))
            }
            return p
        }

        override fun valueAtLong(p: Int): Long {
            return valueAtCoerceToInt(p, null).toLong()
        }

        override fun valueAt(p: Int): APLValue {
            return valueAtLong(p).makeAPLNumber()
        }

        override fun resizeIotaArray(d: Dimensions, updatedOffset: Long): APLValue {
            return ResizedIotaArrayLong(d, length, updatedOffset)
        }
    }

    class ResizedIotaArrayLong(
        override val dimensions: Dimensions,
        val width: Int,
        val offset: Long
    ) : APLArray(), GenericIotaArrayLong {
        override val size = dimensions.contentSize()
        override val specialisedType get() = ArrayMemberType.LONG
        override fun collapseInt(withDiscard: Boolean) = this

        init {
            if (width > 0) {
                try {
                    addExact(width.toLong() - 1, offset)
                } catch (_: ArithmeticException) {
                    error("offset+width does not fit in a long")
                }
            }
        }

        override fun valueAtLong(p: Int): Long {
            return when {
                p < 0 || p >= size -> throwAPLException(APLIndexOutOfBoundsException("Position in array: ${p}, size: ${size}"))
                p < width -> p + offset
                else -> (p % width) + offset
            }
        }

        override fun valueAt(p: Int): APLValue {
            return valueAtLong(p).makeAPLNumber()
        }

        override fun resizeIotaArray(d: Dimensions, updatedOffset: Long): APLValue {
            return if (d.contentSize() == width) {
                ResizedIotaArrayLong(d, width, offset + updatedOffset)
            } else {
                ResizedArrayImpls.GenericResizedArray(d, this)
            }
        }
    }
}

class FindIndexArray1DLeftArg(val a: APLValue, val b: APLValue) : APLArray() {
    override val dimensions = b.dimensions
    override val specialisedType get() = ArrayMemberType.LONG

    override fun valueAt(p: Int): APLValue {
        return valueAtLong(p).makeAPLNumber()
    }

    override fun valueAtLong(p: Int): Long {
        val reference = b.valueAt(p)
        return findFromRef(reference)
    }

    private fun findFromRef(reference: APLValue): Long {
        val refColl = reference.collapse()
        a.iterateMembersWithPosition { v, i ->
            if (v.compareEqualsTotalOrdering(refColl, typeDiscrimination = false)) {
                return i.toLong()
            }
        }
        return a.size.toLong()
    }

    override fun unwrapDeferredValue(): APLValue {
        return if (dimensions.isEmpty()) {
            findFromRef(b.disclose()).makeAPLNumber()
        } else {
            this
        }
    }
}

class FindIndexArrayMultiDimensionalLeftArg(val a: APLValue, val b: APLValue) : APLArray() {
    override val dimensions = b.dimensions
    private val aMultipliers = a.dimensions.multipliers()

    override fun valueAt(p: Int): APLValue {
        val ref = b.valueAt(p).collapse()
        a.iterateMembersWithPosition { v, i ->
            if (v.compareEqualsTotalOrdering(ref, typeDiscrimination = false)) {
                val coord = aMultipliers.positionFromIndex(i)
                return APLArrayInt(dimensionsOfSize(coord.size), coord)
            }
        }
        return APLNilValue
    }
}

class IotaAPLFunction : APLFunctionDescriptor {
    class IotaAPLFunctionImpl(pos: FunctionInstantiation) : NoAxisAPLFunction(pos) {
        override fun eval1Arg(context: RuntimeContext, a: APLValue): APLValue {
            val aDimensions = a.dimensions
            return when (aDimensions.size) {
                0 -> IotaArrayImpls.IotaArrayLong(a.ensureNumber(pos).asInt(pos))
                1 -> if (aDimensions[0] == 0) {
                    EnclosedAPLValue.make(APLNullValue)
                } else {
                    IotaArrayImpls.IotaArray(IntArray(aDimensions[0]) { i -> a.valueAtCoerceToInt(i, pos) })
                }

                else -> throwAPLException(InvalidDimensionsException("Right argument must be rank 0 or 1", pos))
            }
        }

        override fun eval2Arg(context: RuntimeContext, a: APLValue, b: APLValue): APLValue {
            return if (a.rank <= 1) {
                FindIndexArray1DLeftArg(a.arrayify(), b)
            } else {
                FindIndexArrayMultiDimensionalLeftArg(a.arrayify(), b)
            }
        }

        override val name1Arg get() = "iota"
        override val name2Arg get() = "index"
    }

    override fun make(instantiation: FunctionInstantiation) = IotaAPLFunctionImpl(instantiation)
}

object ResizedArrayImpls {
    class GenericResizedArray(override val dimensions: Dimensions, val value: APLValue) : APLArray() {
        override val specialisedType get() = value.specialisedType

        private inline fun valueAtImpl(p: Int, fn: (Int) -> Nothing): Nothing {
            val s = value.size
            val p0 = if (p < s) p else p % s
            fn(p0)
        }

        override fun valueAt(p: Int): APLValue {
            valueAtImpl(p) { p0 ->
                return value.valueAt(p0)
            }
        }

        override fun valueAtCoerceToInt(p: Int, pos: Position?): Int {
            valueAtImpl(p) { p0 ->
                return value.valueAtCoerceToInt(p0, pos)
            }
        }

        override fun valueAtLong(p: Int): Long {
            valueAtImpl(p) { p0 ->
                return value.valueAtLong(p0)
            }
        }

        override fun valueAtDouble(p: Int): Double {
            valueAtImpl(p) { p0 ->
                return value.valueAtDouble(p0)
            }
        }
    }

    class ResizedSingleValueGeneric(override val dimensions: Dimensions, private val value: APLValue) : APLArray() {
        override val specialisedType get() = value.specialisedTypeAsMember

        override fun valueAt(p: Int) = value

        override fun valueAtLong(p: Int): Long {
            return when (value) {
                is APLLong -> value.value
                is APLBigInt -> throw LongExpressionOverflow(value.value)
                else -> throw IllegalStateException("Trying to read integer result from a non-integer value: ${value::class.simpleName}")
            }
        }

        override fun valueAtDouble(p: Int): Double {
            if (value !is APLDouble) {
                throw IllegalStateException("Trying to read double result from a non-double value: ${value::class.simpleName}")
            }
            return value.value
        }
    }

    class ResizedSingleValueLong(override val dimensions: Dimensions, private val boxed: APLLong) : APLArray() {
        override val specialisedType get() = boxed.value.let { v -> if (v == 0L || v == 1L) ArrayMemberType.BOOLEAN else ArrayMemberType.LONG }
        override fun valueAt(p: Int) = boxed
        override fun valueAtLong(p: Int) = boxed.value
    }

    class ResizedSingleValueDouble(override val dimensions: Dimensions, private val boxed: APLDouble) : APLArray() {
        override val specialisedType get() = ArrayMemberType.DOUBLE
        override fun valueAt(p: Int) = boxed
        override fun valueAtDouble(p: Int) = boxed.value
    }

    class ResizedArrayEqualLength(override val dimensions: Dimensions, private val value: APLValue) : APLArray() {
        override val specialisedType get() = value.specialisedType

        override fun valueAt(p: Int) = value.valueAt(p)
        override fun valueAtLong(p: Int) = value.valueAtLong(p)
        override fun valueAtDouble(p: Int) = value.valueAtDouble(p)
    }

    private fun findUnderlyingArray(a: APLValue, requestedSize: Int): APLValue {
        var curr = a.unwrapDeferredValue()
        // We can only optimise resized arrays whose size is identical to the new size.
        // If array sizes are different, the resulting array has modular indexes which
        // would not be preserved.
        while (true) {
            curr = when {
                curr is GenericResizedArray && curr.size == requestedSize -> curr.value
                curr is MetadataOverrideArray -> curr.value
                else -> break
            }
        }
        return curr
    }

    fun makeResizedArray(dimensions: Dimensions, value: APLValue): APLValue {
        require(dimensions.size >= 1)
        val v0 = findUnderlyingArray(value, dimensions.contentSize())
        val dimensionsSize = dimensions.contentSize()
        val resized = when {
            dimensions.contentSize() == 0 -> APLEmptyArray(dimensions)
            dimensions.compareEquals(v0.dimensions) -> v0
            value is IotaArrayImpls.GenericIotaArrayLong -> value.resizeIotaArray(dimensions, 0)
            v0 is APLSingleValue -> resizedSingleValue(dimensions, v0)
            dimensions.size == 0 -> resizedSingleValue(dimensions, v0.disclose())
            else -> {
                when (v0.size) {
                    0 -> resizedSingleValue(dimensions, value.metadata.defaultValue)
                    dimensionsSize -> ResizedArrayEqualLength(dimensions, v0)
                    else -> GenericResizedArray(dimensions, v0)
                }
            }
        }
        val def = value.metadata.defaultValue
        return if (def === APLValueMetadata.DefaultMetadata.defaultValue) {
            resized
        } else {
            AssignPrototypeFunction.AssignPrototypeFunctionImpl.wrapWithPrototype(resized, def)
        }
    }

    fun resizedSingleValue(dimensions: Dimensions, v: APLValue): APLValue {
        return when (v) {
            is APLLong -> ResizedSingleValueLong(dimensions, v)
            is APLDouble -> ResizedSingleValueDouble(dimensions, v)
            else -> ResizedSingleValueGeneric(dimensions, v)
        }
    }
}

class RhoAPLFunction : APLFunctionDescriptor {
    class RhoAPLFunctionImpl(pos: FunctionInstantiation) : NoAxisAPLFunction(pos) {
        override fun eval1Arg(context: RuntimeContext, a: APLValue): APLValue {
            val argDimensions = a.dimensions
            return APLArrayImpl.make(dimensionsOfSize(argDimensions.size)) { argDimensions[it].makeAPLNumber() }
        }

        override fun eval2Arg(context: RuntimeContext, a: APLValue, b: APLValue): APLValue {
            val d = a.dimensions

            if (d.size > 1) {
                throwAPLException(InvalidDimensionsException("Left side of rho must be scalar or a one-dimensional array", pos))
            }

            val v = a.unwrapDeferredValue()
            if (d.size == 1 && d[0] == 0) {
                return EnclosedAPLValue.make(b.arrayify().valueAt(0))
            } else {
                if (d.size == 0) {
                    val v0 = v.unwrapDeferredValue()
                    val calculatedType = findSizeCalculationMethod(v0)
                    val d1 = if (calculatedType != null) {
                        dimensionsOfSize(b.size)
                    } else {
                        val s = v0.ensureNumber(pos).asInt()
                        if (s < 0) {
                            throwAPLException(InvalidDimensionsException("Attempt to reshape to dimension with negative size: ${s}", pos))
                        } else {
                            dimensionsOfSize(s)
                        }
                    }
                    return ResizedArrayImpls.makeResizedArray(d1, b)
                } else {
                    var calculationMethod: SizeCalculationMethod? = null
                    var totalSize = 1
                    val resultDimensions = IntArray(v.dimensions[0]) { i ->
                        val v1 = v.valueAt(i).unwrapDeferredValue()
                        val calculatedType = findSizeCalculationMethod(v1)
                        if (calculatedType != null) {
                            if (calculationMethod != null) {
                                throwAPLException(InvalidDimensionsException("Only a single dimension is allowed to be marked as computed", pos))
                            }
                            calculationMethod = calculatedType
                            -1
                        } else {
                            v1.ensureNumber(pos).asInt(pos).also { n ->
                                totalSize *= n
                            }
                        }
                    }
                    if (calculationMethod == null) {
                        return ResizedArrayImpls.makeResizedArray(Dimensions(resultDimensions), b)
                    } else {
                        val bDimensions = b.dimensions
                        if (bDimensions.size == 0) {
                            throwAPLException(APLIllegalArgumentException("Calculated dimensions can only be used with array arguments", pos))
                        }
                        val bContentSize = bDimensions.contentSize()
                        val calculatedSize = bContentSize / totalSize
                        val calculatedRemainder = bContentSize % totalSize

                        fun makeResult(updatedCalculatedSize: Int, updatedSrc: APLValue): APLValue {
                            val newDimensions = IntArray(v.dimensions[0]) { i -> resultDimensions[i].let { s -> if (s == -1) updatedCalculatedSize else s } }
                            return ResizedArrayImpls.makeResizedArray(Dimensions(newDimensions), updatedSrc)
                        }

                        if (calculatedRemainder == 0) {
                            return makeResult(calculatedSize, b)
                        }

                        when (calculationMethod) {
                            SizeCalculationMethod.MATCH -> {
                                if (calculatedRemainder != 0) {
                                    throwAPLException(
                                        InvalidDimensionsException(
                                            "Invalid size of right argument: ${bContentSize}. Should be divisible by ${totalSize}.",
                                            pos))
                                }
                                return makeResult(calculatedSize, b)
                            }
                            SizeCalculationMethod.FILL -> {
                                return makeResult(calculatedSize + 1, ExtendedRavelArray(b, (calculatedSize + 1) * totalSize, pos))
                            }
                            SizeCalculationMethod.TRUNCATE -> {
                                return makeResult(calculatedSize, b)
                            }
                            SizeCalculationMethod.RECYCLE -> {
                                return makeResult(calculatedSize + 1, b)
                            }
                        }
                    }
                }
            }
        }

        override fun evalWithStructuralUnder2Arg(baseFn: APLFunction, context: RuntimeContext, a: APLValue, b: APLValue): APLValue {
            val bDimensions = b.dimensions
            val underValue = eval2Arg(context, a, b, null)
            val underValueDimensions = underValue.dimensions
            val aContentSize = bDimensions.contentSize()
            val underValueContentSize = underValueDimensions.contentSize()
            if (aContentSize < underValueContentSize) {
                throwAPLException(
                    InvalidDimensionsException(
                        "When performing an operation under a reshape, the new shape must be smaller than or equal to the original shape",
                        pos))
            }
            val updated = baseFn.eval1Arg(context, underValue, null)
            unless(updated.dimensions.compareEquals(underValueDimensions)) {
                throwAPLException(
                    InvalidDimensionsException(
                        "Result does not have the same dimensions as input. Expected: ${underValue.dimensions}, got: ${updated.dimensions}",
                        pos))
            }
            return if (aContentSize == underValueContentSize) {
                ResizedArrayImpls.makeResizedArray(bDimensions, updated)
            } else {
                ResizedArrayImpls.makeResizedArray(
                    bDimensions,
                    Concatenated1DArrays(
                        ResizedArrayImpls.makeResizedArray(dimensionsOfSize(underValueContentSize), updated),
                        DropArrayValue(
                            intArrayOf(underValueContentSize),
                            ResizedArrayImpls.makeResizedArray(dimensionsOfSize(aContentSize), b),
                            pos)))
            }
        }

        override fun deriveBitwise() = BitwiseBitLengthFunction()

        override val name1Arg get() = "shape"
        override val name2Arg get() = "reshape"

        private fun findSizeCalculationMethod(a: APLValue): SizeCalculationMethod? {
            if (a is APLNumber && a.asLong() == -1L) {
                return SizeCalculationMethod.MATCH
            }
            if (a !is APLSymbol) {
                return null
            }
            val sym = a.value
            if (sym.namespace !== NamespaceList.KEYWORD_NAMESPACE_NAME) {
                return null
            }
            return when (sym.symbolName) {
                "match" -> SizeCalculationMethod.MATCH
                "fill" -> SizeCalculationMethod.FILL
                "truncate" -> SizeCalculationMethod.TRUNCATE
                "recycle" -> SizeCalculationMethod.RECYCLE
                else -> null
            }
        }

        private enum class SizeCalculationMethod {
            MATCH, FILL, TRUNCATE, RECYCLE
        }
    }

    override fun make(instantiation: FunctionInstantiation) = RhoAPLFunctionImpl(instantiation)
}

/**
 * A single-dimensional array that wraps array [src], but which has size [newSize].
 * Getting any value outside the size of the original array returns the fill element.
 *
 * This array can be seen as a simplified form of the result of take of the ravel
 * of the source.
 */
class ExtendedRavelArray(val src: APLValue, newSize: Int, val pos: Position) : APLArray() {
    override val dimensions = dimensionsOfSize(newSize)
    private val srcSize = src.dimensions.contentSize()
    private val fillElement = src.metadata.defaultValue
    override val specialisedType = src.specialisedType.intersection(fillElement.specialisedTypeAsMember)

    init {
        require(src.dimensions.contentSize() < newSize)
    }

    override fun valueAt(p: Int): APLValue {
        return if (p < srcSize) {
            src.valueAt(p)
        } else {
            fillElement
        }
    }

    override fun valueAtLong(p: Int): Long {
        return if (p < srcSize) {
            src.valueAtLong(p)
        } else {
            fillElement.ensureNumber(pos).asLong(pos)
        }
    }

    override fun valueAtDouble(p: Int): Double {
        return if (p < srcSize) {
            src.valueAtDouble(p)
        } else {
            fillElement.ensureNumber(pos).asDouble(pos)
        }
    }
}

class IdentityAPLFunction : APLFunctionDescriptor {
    class IdentityAPLFunctionImpl(pos: FunctionInstantiation) : NoAxisAPLFunction(pos) {
        override fun eval1Arg(context: RuntimeContext, a: APLValue) = a
        override fun eval2Arg(context: RuntimeContext, a: APLValue, b: APLValue) = b

        override fun eval1ArgLong(context: RuntimeContext, a: Long, axis: APLValue?) = a
        override fun eval1ArgDouble(context: RuntimeContext, a: Double, axis: APLValue?) = a
        override fun eval2ArgLongToLongWithAxis(context: RuntimeContext, a: Long, b: Long, axis: APLValue?) = b
        override fun eval2ArgDoubleToDoubleWithAxis(context: RuntimeContext, a: Double, b: Double, axis: APLValue?) = b

        override fun evalInverse1Arg(context: RuntimeContext, a: APLValue) = a
        override fun evalInverse2ArgB(context: RuntimeContext, a: APLValue, b: APLValue) = b

        override fun evalWithStructuralUnder1Arg(baseFn: APLFunction, context: RuntimeContext, a: APLValue): APLValue {
            return baseFn.eval1Arg(context, a, null)
        }

        override fun evalWithStructuralUnder2Arg(baseFn: APLFunction, context: RuntimeContext, a: APLValue, b: APLValue): APLValue {
            return baseFn.eval1Arg(context, b, null)
        }

        override val optimisationFlags: OptimisationFlags
            get() = OptimisationFlags(
                OptimisationFlags.OPTIMISATION_FLAG_1ARG_LONG or
                        OptimisationFlags.OPTIMISATION_FLAG_1ARG_DOUBLE or
                        OptimisationFlags.OPTIMISATION_FLAG_2ARG_LONG_LONG or
                        OptimisationFlags.OPTIMISATION_FLAG_2ARG_DOUBLE_DOUBLE)

        override val name1Arg get() = "identity"
        override val name2Arg get() = "right"
    }

    override fun make(instantiation: FunctionInstantiation) = IdentityAPLFunctionImpl(instantiation)
}

class HideAPLFunction : APLFunctionDescriptor {
    class HideAPLFunctionImpl(pos: FunctionInstantiation) : NoAxisAPLFunction(pos) {
        override fun eval1Arg(context: RuntimeContext, a: APLValue): APLValue {
            a.collapse(withDiscard = true)
            return EmptyValue
        }

        override fun eval2Arg(context: RuntimeContext, a: APLValue, b: APLValue) = a

        override fun eval1ArgLong(context: RuntimeContext, a: Long, axis: APLValue?) = a
        override fun eval1ArgDouble(context: RuntimeContext, a: Double, axis: APLValue?) = a
        override fun eval2ArgLongToLongWithAxis(context: RuntimeContext, a: Long, b: Long, axis: APLValue?) = a
        override fun eval2ArgDoubleToDoubleWithAxis(context: RuntimeContext, a: Double, b: Double, axis: APLValue?) = a

        override fun evalInverse2ArgA(context: RuntimeContext, a: APLValue, b: APLValue) = a

        override val optimisationFlags: OptimisationFlags
            get() = OptimisationFlags(
                OptimisationFlags.OPTIMISATION_FLAG_1ARG_LONG or
                        OptimisationFlags.OPTIMISATION_FLAG_1ARG_DOUBLE or
                        OptimisationFlags.OPTIMISATION_FLAG_2ARG_LONG_LONG or
                        OptimisationFlags.OPTIMISATION_FLAG_2ARG_DOUBLE_DOUBLE)

        override val name1Arg get() = "identity"
        override val name2Arg get() = "left"
    }

    override fun make(instantiation: FunctionInstantiation) = HideAPLFunctionImpl(instantiation)
}

class AccessFromIndexAPLFunction : APLFunctionDescriptor {
    class AccessFromIndexAPLFunctionImpl(pos: FunctionInstantiation) : APLFunction(pos) {
        private val fromListFunction by lazy { FromListFunction.FromListFunctionImpl(pos) }

        override fun eval1Arg(context: RuntimeContext, a: APLValue, axis: APLValue?): APLValue {
            return fromListFunction.eval1Arg(context, a.listify(), axis)
        }

        private val toListFunction by lazy { ToListFunction.ToListFunctionImpl(pos) }

        override fun evalInverse1Arg(context: RuntimeContext, a: APLValue, axis: APLValue?): APLValue {
            return toListFunction.eval1Arg(context, a, axis)
        }

        override fun eval2Arg(context: RuntimeContext, a: APLValue, b: APLValue, axis: APLValue?): APLValue {
            val aFixed = a.arrayify()
            val ad = aFixed.dimensions

            if (ad.size != 1) {
                throwAPLException(InvalidDimensionsException("Position argument is not rank 1", pos))
            }

            val bd = b.dimensions
            val indexList = if (axis == null) {
                if (ad[0] > bd.size) {
                    throwAPLException(
                        InvalidDimensionsException(
                            "Number of values in position argument must be less than on equal to the number of dimensions",
                            pos))
                }
                Array(bd.size) { i ->
                    val axisSize = bd[i]
                    if (i < ad[0]) {
                        val indexValue = aFixed.valueAt(i)
                        when {
                            indexValue.dimensions.size > 0 -> {
                                Either.Right(IntArrayValue.fromAPLValue(indexValue, pos) { v -> Dimensions.checkAndAdjustSelectedIndex(v, axisSize, pos) })
                            }
                            indexValue == APLNilValue -> {
                                makeAllIndexList(axisSize)
                            }
                            else -> {
                                Either.Left(Dimensions.checkAndAdjustSelectedIndex(indexValue.ensureNumber(pos).asInt(pos), axisSize, pos))
                            }
                        }
                    } else {
                        makeAllIndexList(axisSize)
                    }
                }
            } else {
                if (axis.dimensions.size > 1) {
                    throwAPLException(InvalidDimensionsException("Axis argument must be a scalar number or a 1-dimensional array of numbers", pos))
                }
                val list = ArrayList<Either<Int, IntArrayValue>?>()
                repeat(bd.size) { list.add(null) }
                val axesArray = axis.arrayify().toIntArray(pos)
                if (axesArray.any { it < 0 || it >= bd.size }) {
                    throwAPLException(
                        IllegalAxisException("Invalid axis in axis specification", pos).details(
                            """
                                    The axis specification was [${axesArray.joinToString(", ")}], this argument must be an
                                    array of integers where each value must be greater or equal to 0, and less than the
                                    number of dimensions of the right argument: ${bd.size}.
                                """.reformatLines()
                        ))
                }
                if (containsDuplicates(axesArray)) {
                    throwAPLException(IllegalAxisException("Duplicated axis in axis specification", pos))
                }
                if (ad[0] != axesArray.size) {
                    throwAPLException(
                        IllegalAxisException(
                            "Number of values in position argument must match the number of axes in axis specification", pos).details(
                            """
                                The axis argument contains ${ad[0]} values. The axis specification is [${axesArray.joinToString(", ")}], which
                                is of size ${axesArray.size}.
                            """.reformatLines()
                        ))
                }
                aFixed.iterateMembersWithPosition { m, p ->
                    val axisInt = axesArray[p]
                    val axisSize = bd[axisInt]
                    val v = when {
                        m.dimensions.size != 0 -> {
                            Either.Right(IntArrayValue.fromAPLValue(m, pos) { v -> Dimensions.checkAndAdjustSelectedIndex(v, axisSize, pos) })
                        }
                        m == APLNilValue -> {
                            makeAllIndexList(axisSize)
                        }
                        else -> {
                            Either.Left(Dimensions.checkAndAdjustSelectedIndex(m.ensureNumber(pos).asInt(pos), axisSize, pos))
                        }
                    }
                    list[axisInt] = v
                }
                Array(bd.size) { i ->
                    list[i] ?: makeAllIndexList(bd[i])
                }
            }
            return IndexedArrayValue.make(b, indexList)
        }

        private fun makeAllIndexList(size: Int): Either.Right<IntArrayValue> {
            return Either.Right(IntArrayValue(dimensionsOfSize(size), IntArray(size) { it }))
        }

        private fun containsDuplicates(values: IntArray): Boolean {
            for (i0 in 1 until values.size) {
                val v0 = values[i0]
                for (i1 in 0 until i0) {
                    if (values[i1] == v0) return true
                }
            }
            return false
        }

        override val name1Arg get() = fromListFunction.name1Arg
        override val name2Arg get() = "lookup index"
    }

    override fun make(instantiation: FunctionInstantiation) = AccessFromIndexAPLFunctionImpl(instantiation)
}

class PickResultValue(val a: APLValue, val b: APLValue, val pos: Position) : APLArray() {
    override val dimensions = a.dimensions
    val bDimensions = b.dimensions
    override val specialisedType = b.specialisedType

    override fun valueAt(p: Int) = b.valueAt(computePos(p))
    override fun valueAtLong(p: Int) = b.valueAtLong(computePos(p))
    override fun valueAtDouble(p: Int) = b.valueAtDouble(computePos(p))

    private fun computePos(p: Int): Int {
        val indexCoord = a.valueAt(p)
        return indexCoordToPosition(indexCoord)
    }

    private fun indexCoordToPosition(indexCoord: APLValue): Int {
        val indexCoordDimensions = indexCoord.dimensions
        if (!((indexCoordDimensions.size == 0 && bDimensions.size == 1) || (indexCoordDimensions.size == 1 && bDimensions.size == indexCoordDimensions[0]))) {
            throwAPLException(
                InvalidDimensionsException(
                    "Lookup index has rank ${indexCoordDimensions.size}. Not compatible with rank: ${bDimensions.size}",
                    pos))
        }
        val coordArray = if (indexCoordDimensions.size == 0) intArrayOf(indexCoord.ensureNumber(pos).asInt(pos)) else indexCoord.toIntArray(pos)
        return bDimensions.indexFromPositionNegativeSupport(coordArray, pos)
    }

    override fun unwrapDeferredValue(): APLValue {
        return unwrapEnclosedSingleValue(this)
    }

    fun replaceForUnder(updated: APLValue): APLValue {
        if (!updated.dimensions.compareEquals(dimensions)) {
            throwAPLException(
                InvalidDimensionsException(
                    "Updated result does not have the same dimensions as selection. Updated: ${updated.dimensions}, selection: ${dimensions}",
                    pos))
        }
        return if (size == 0) {
            b
        } else {
            val positionList = IntArray(bDimensions.contentSize())
            positionList.fill(-1)
            a.iterateMembersWithPosition { indexCoord, i ->
                val resultPos = indexCoordToPosition(indexCoord)
                positionList[resultPos] = i
            }
            PickOverlayReplacementValue(bDimensions, updated, b, positionList)
        }
    }
}

class PickOverlayReplacementValue(override val dimensions: Dimensions, val updated: APLValue, val b: APLValue, val positionList: IntArray) : APLArray() {
    override val specialisedType: ArrayMemberType
        get() {
            val uType = updated.specialisedType
            val bType = b.specialisedType
            return bType.intersection(uType)
        }

    private inline fun valueAtInternal(p: Int, fn: (v: APLValue, position: Int) -> Nothing): Nothing {
        val position = positionList[p]
        if (position == -1) fn(b, p) else fn(updated, position)
    }

    override fun valueAt(p: Int): APLValue {
        valueAtInternal(p) { v, position ->
            return v.valueAt(position)
        }
    }

    override fun valueAtLong(p: Int): Long {
        valueAtInternal(p) { v, position ->
            return v.valueAtLong(position)
        }
    }

    override fun valueAtDouble(p: Int): Double {
        valueAtInternal(p) { v, position ->
            return v.valueAtDouble(position)
        }
    }
}

class PickAPLFunction : APLFunctionDescriptor {
    class PickAPLFunctionImpl(pos: FunctionInstantiation) : NoAxisAPLFunction(pos) {
        override fun eval2Arg(context: RuntimeContext, a: APLValue, b: APLValue): APLValue {
            return PickResultValue(a.unwrapDeferredValue(), b, pos)
        }

        override fun evalWithStructuralUnder2Arg(baseFn: APLFunction, context: RuntimeContext, a: APLValue, b: APLValue): APLValue {
            val a0 = a.collapse()
            val underValue = eval2Arg(context, a0, b, null)
            require(underValue is PickResultValue) { "Result is not of the correct type. Type = ${underValue::class}" }
            val updated = baseFn.eval1Arg(context, underValue, null)
            val updated0 = if (updated.isScalar() && !underValue.isScalar()) {
                ResizedArrayImpls.makeResizedArray(underValue.dimensions, updated)
            } else {
                updated
            }
            return underValue.replaceForUnder(updated0)
        }

        override val name2Arg get() = "pick"
    }

    override fun make(instantiation: FunctionInstantiation) = PickAPLFunctionImpl(instantiation)
}


class TakeArrayValue(val selection: IntArray, val source: APLValue, val pos: Position? = null) : APLArray() {
    override val dimensions = Dimensions(selection.map(Int::absoluteValue).toIntArray())
    override val metadata by lazy { maybeDefaultMetadata(source.metadata) { m -> TakeArrayValueMetadata(m, selection) } }

    private val needsFill by lazy { computeNeedsFill() }

    override val specialisedType by lazy { if (needsFill) source.specialisedType.intersection(metadata.defaultValue.specialisedTypeAsMember) else source.specialisedType }

    private val multipliers = dimensions.multipliers()
    private val sourceDimensions = source.dimensions
    private val sourceMultipliers = sourceDimensions.multipliers()

    private fun computeNeedsFill(): Boolean {
        source.dimensions.forEachIndexed { i, size ->
            if (selection[i].absoluteValue > size) {
                return true
            }
        }
        return false
    }

    private inline fun <T> resolveIndex(p: Int, valueFromSource: (Int) -> T, valueFromDefault: () -> T): T {
        val srcIndex = when (multipliers.size) {
            0 -> 0
            1 -> {
                val d = selection[0]
                val size = sourceDimensions[0]
                val index = if (d >= 0) p else size + d + p
                if (index < 0 || index >= size) {
                    return valueFromDefault()
                } else {
                    index
                }
            }
            else -> {
                val coords = multipliers.positionFromIndex(p)
                var curr = 0
                coords.forEachIndexed { i, v ->
                    val d = selection[i]
                    val di = sourceDimensions[i]
                    val pi = if (d >= 0) v else di + d + v
                    if (pi < 0 || pi >= di) {
                        return valueFromDefault()
                    }
                    curr += pi * sourceMultipliers[i]
                }
                curr
            }
        }
        return valueFromSource(srcIndex)
    }

    override fun valueAt(p: Int): APLValue {
        return resolveIndex(p, { i -> source.valueAt(i) }, { source.metadata.defaultValue })
    }

    override fun valueAtLong(p: Int): Long {
        return resolveIndex(p, { i -> source.valueAtLong(i) }, { source.metadata.defaultValue.ensureNumber(pos).asLong(pos) })
    }

    override fun valueAtDouble(p: Int): Double {
        return resolveIndex(p, { i -> source.valueAtDouble(i) }, { source.metadata.defaultValue.ensureNumber(pos).asDouble(pos) })
    }

    fun replaceForUnder(replacement: APLValue): APLValue {
        val offset = IntArray(dimensions.size) { i ->
            val selectionValue = selection[i]
            if (selectionValue >= 0) {
                0
            } else {
                sourceDimensions[i] + selectionValue
            }
        }
        return OverlayReplacementValue(source, dimensions, replacement, offset, pos)
    }

    class TakeArrayValueMetadata(val sourceMetadata: APLValueMetadata, val selection: IntArray) : APLValueMetadata {
        override val labels by lazy { resolveLabels() }
        override val defaultValue get() = sourceMetadata.defaultValue

        private fun resolveLabels(): DimensionLabels? {
            return computeTakeDropLabels(sourceMetadata, selection) { axisLabels, selected ->
                if (selected >= 0) {
                    if (selected <= axisLabels.size) {
                        axisLabels.subList(0, selected)
                    } else {
                        val res = ArrayList<AxisLabel?>()
                        res.addAll(axisLabels)
                        repeat(selected - axisLabels.size) {
                            res.add(null)
                        }
                        res
                    }
                } else {
                    if (-selected <= axisLabels.size) {
                        axisLabels.subList(axisLabels.size + selected, axisLabels.size)
                    } else {
                        val res = ArrayList<AxisLabel?>()
                        repeat((-selected) - axisLabels.size) {
                            res.add(null)
                        }
                        res.addAll(axisLabels)
                        res
                    }
                }
            }
        }
    }
}

class OverlayReplacementValue(
    val src: APLValue,
    val srcReplacementDimensions: Dimensions,
    val replacement: APLValue,
    val offset: IntArray,
    pos: Position?
) : APLArray() {
    private val srcDimensions: Dimensions
    private val replacementDimensions: Dimensions

    override val dimensions: Dimensions

    private val multipliers: Dimensions.DimensionMultipliers

    init {
        srcDimensions = src.dimensions
        replacementDimensions = replacement.dimensions

        if (replacementDimensions.size != srcDimensions.size) {
            throwAPLException(
                InvalidDimensionsException(
                    "Replacement value must have the same rank as the original data. Got rank=${replacementDimensions.size}, original=${srcDimensions.size}",
                    pos))
        }

        dimensions = Dimensions(IntArray(srcReplacementDimensions.size) { i ->
            srcDimensions[i] - srcReplacementDimensions[i] + replacementDimensions[i]
        })

        multipliers = dimensions.multipliers()

        if (dimensions.size != offset.size) {
            throw IllegalStateException("offset size does not match src dimensions")
        }
        if (dimensions.size != replacementDimensions.size) {
            throwAPLException(InvalidDimensionsException("replacement array rank mismatch", pos))
        }

        // After the below loop, resizableDimension will have the following value:
        //   positive integer: The dimension that can be resized
        //   null: All dimensions can be resized
        //   -1: No dimensions can be resized
        var resizableDimension: Int? = null
        repeat(dimensions.size) { i ->
            // If all but a single dimension extends over the entire range, then that dimension can be resized
            if (replacementDimensions[i] != srcDimensions[i]) {
                resizableDimension = if (resizableDimension == null) i else -1
            }
        }
        repeat(dimensions.size) { i ->
            if (resizableDimension != null) {
                if (srcReplacementDimensions[i] != replacementDimensions[i] && i != resizableDimension) {
                    throwAPLException(InvalidDimensionsException("cannot resize axis: ${i}", pos))
                }
            }
            if (dimensions[i] < offset[i] + replacementDimensions[i]) {
                throwAPLException(InvalidDimensionsException("replacement array size overflows at index ${i}", pos))
            }
        }
    }

    override val metadata by lazy {
        if (src.metadata.isDefault && replacement.metadata.isDefault) {
            APLValueMetadata.DefaultMetadata
        } else {
            OverlayReplacementValueMetadata(
                srcDimensions, src.metadata, srcReplacementDimensions,
                replacementDimensions, replacement.metadata, offset)
        }
    }

    override fun valueAt(p: Int): APLValue {
        val posArray = multipliers.positionFromIndex(p)
        return if (isWithinReplacement(posArray)) {
            val newPosArray = IntArray(posArray.size) { i -> posArray[i] - offset[i] }
            replacement.valueAt(replacementDimensions.indexFromPosition(newPosArray))
        } else {
            val newPosArray = IntArray(posArray.size) { i ->
                val posOnAxis = posArray[i]
                if (posOnAxis < offset[i]) {
                    posOnAxis
                } else {
                    posOnAxis - replacementDimensions[i] + srcReplacementDimensions[i]
                }
            }
            src.valueAt(srcDimensions.indexFromPosition(newPosArray))
        }
    }

    private fun isWithinReplacement(posArray: IntArray): Boolean {
        repeat(dimensions.size) { i ->
            val p = posArray[i]
            val offsetValue = offset[i]
            if (p < offsetValue || p >= replacementDimensions[i] + offsetValue) {
                return false
            }
        }
        return true
    }

    class OverlayReplacementValueMetadata(
        private val srcDimensions: Dimensions,
        private val srcMetadata: APLValueMetadata,
        private val srcReplDimensions: Dimensions,
        private val replDimensions: Dimensions,
        private val replMetadata: APLValueMetadata,
        private val offsetList: IntArray
    ) : APLValueMetadata {
        override val labels by lazy { resolveLabels() }

        // There is a conflict when merging the labels from the source and replacement arrays.
        //
        // In the following example, we are assuming the 100-series numbers are the replacement values:
        //
        // ┌→──────────┐
        // ↓  0   1   2│
        // │100 101 102│
        // │103 104 105│
        // │106 107 108│
        // │  3   4   5│
        // │  6   7   8│
        // │  0   1   2│
        // └───────────┘
        //
        // It is very clear that  we can simply take the row labels from the replacement axis,
        // since none of the original data remains.
        //
        // In the case of the column labels, we can see the conflict, because the replacement
        // spans the entire row, but it still doesn't make much sense to use the replacement
        // labels in this case. To resolve this, we check if the replacement spans all the values
        // in all the other axes, and if so, use the replacement labels. Otherwise, use
        // the source labels.
        private fun resolveLabels(): DimensionLabels? {
            require(srcDimensions.size == replDimensions.size)
            val srcLabels = srcMetadata.labels
            val replLabels = replMetadata.labels
            if (srcLabels == null && replLabels == null) {
                return null
            }

            val resultList = ArrayList<List<AxisLabel?>?>()
            srcDimensions.indices.forEach { axisIndex ->
                val isResized = srcReplDimensions[axisIndex] != replDimensions[axisIndex]
                val srcAxisLabels = srcLabels?.let { l -> l.labels[axisIndex] }
                val replAxisLabels = replLabels?.let { l -> l.labels[axisIndex] }
                val res = when {
                    srcAxisLabels == null && replAxisLabels == null -> {
                        null
                    }
                    isResized -> {
                        val srcSize = srcDimensions[axisIndex]
                        val srcReplSize = srcReplDimensions[axisIndex]
                        val replSize = replDimensions[axisIndex]
                        val offsetIndex = offsetList[axisIndex]
                        val updatedLabels = ArrayList<AxisLabel?>()

                        repeat(srcSize - srcReplSize + replSize) { labelIndex ->
                            val label = when {
                                labelIndex < offsetIndex -> srcAxisLabels?.let { l -> l[labelIndex] }
                                labelIndex < offsetIndex + replSize -> replAxisLabels?.let { l -> l[labelIndex - offsetIndex] }
                                else -> srcAxisLabels?.let { l -> l[labelIndex - replSize + srcReplSize] }
                            }
                            updatedLabels.add(label)
                        }
                        updatedLabels
                    }
                    else -> srcAxisLabels
                }
                resultList.add(res)
            }
            return DimensionLabels(resultList)
        }
    }
}

class TakeAPLFunction : APLFunctionDescriptor {
    class TakeAPLFunctionImpl(pos: FunctionInstantiation) : APLFunction(pos) {
        override fun eval1Arg(context: RuntimeContext, a: APLValue, axis: APLValue?): APLValue {
            if (axis != null) {
                throwAPLException(AxisNotSupported(pos))
            }
            val v = a.unwrapDeferredValue()
            return when {
                v is APLSingleValue -> v
                v.isScalar() -> v.valueAt(0)
                v.size == 0 -> v.metadata.defaultValue
                else -> v.valueAt(0)
            }
        }

        override fun eval2Arg(context: RuntimeContext, a: APLValue, b: APLValue, axis: APLValue?): APLValue {
            val (selection, res) = if (axis == null) {
                val a0 = a.arrayify()
                val aDimensions = a0.dimensions
                if (aDimensions.size != 1) {
                    throwAPLException(InvalidDimensionsException("A must be a scalar or one-dimensional array", pos))
                }
                val b0 = if (b.dimensions.isEmpty()) {
                    ResizedArrayImpls.resizedSingleValue(Dimensions(IntArray(a0.size) { 1 }), b.disclose())
                } else {
                    b
                }
                val bDimensions = b0.dimensions
                val numSelections = aDimensions[0]
                if (numSelections > bDimensions.size) {
                    throwAPLException(InvalidDimensionsException("Size of A must be less than or equal to the rank of B", pos))
                }
                val sel = IntArray(bDimensions.size) { i ->
                    if (i < numSelections) {
                        val s = a0.valueAt(i).unwrapDeferredValue()
                        if (s is APLNilValue) {
                            bDimensions[i]
                        } else {
                            s.ensureNumber(pos).asInt(pos)
                        }
                    } else {
                        bDimensions[i]
                    }
                }
                Pair(sel, b0)
            } else {
                val b0 = b.arrayify()
                val axisInt = axis.ensureNumber(pos).asInt()
                val a0 = a.unwrapDeferredValue()
                val a0Dimensions = a0.dimensions
                val argInteger = when {
                    a0Dimensions.size == 0 -> a0.ensureNumber(pos).asInt()
                    a0Dimensions.size == 1 && a0Dimensions[0] == 1 -> a0.valueAtCoerceToInt(0, pos)
                    else -> throwAPLException(APLIllegalArgumentException("When given an explicit axis, the left argument must be a single integer", pos))
                }
                val bDimensions = b0.dimensions
                ensureValidAxis(axisInt, bDimensions, pos)
                val sel = IntArray(bDimensions.size) { i ->
                    if (i == axisInt) {
                        argInteger
                    } else {
                        bDimensions[i]
                    }
                }
                Pair(sel, b0)
            }
            return TakeArrayValue(selection, res, pos)
        }

        override fun evalWithStructuralUnder1Arg(baseFn: APLFunction, context: RuntimeContext, a: APLValue, axis: APLValue?): APLValue {
            val underValue = eval1Arg(context, a, axis)
            val updated = baseFn.eval1Arg(context, underValue, null)
            return if (a.size == 0) {
                a
            } else {
                val replacementDimensions = Dimensions(IntArray(a.dimensions.size) { 1 })
                val replacement = APLArrayImpl(replacementDimensions, arrayOf(updated))
                OverlayReplacementValue(a, replacementDimensions, replacement, IntArray(a.dimensions.size) { 0 }, pos)
            }
        }

        override fun evalWithStructuralUnder2Arg(baseFn: APLFunction, context: RuntimeContext, a: APLValue, b: APLValue, axis: APLValue?): APLValue {
            val underValue = eval2Arg(context, a, b, axis)
            require(underValue is TakeArrayValue) { "Result is not of the correct type. Type = ${underValue::class}" }
            val updated = baseFn.eval1Arg(context, underValue, null)
            return underValue.replaceForUnder(updated)
        }

        override val name1Arg get() = "take"
        override val name2Arg get() = "take"
    }

    override fun make(instantiation: FunctionInstantiation) = TakeAPLFunctionImpl(instantiation)
}

class DropArrayValue(val selection: IntArray, val source: APLValue, val pos: Position) : APLArray() {
    private val sourceDimensions = source.dimensions
    override val dimensions =
        Dimensions(selection.mapIndexed { index, v -> max(0, sourceDimensions[index] - v.absoluteValue) }.toIntArray())
    override val specialisedType get() = source.specialisedType
    private val dimensionsMultipliers = dimensions.multipliers()

    override val metadata by lazy { maybeDefaultMetadata(source.metadata) { m -> DropArrayValueMetadata(m, selection) } }

    override fun valueAt(p: Int): APLValue {
        return source.valueAt(computeIndex(p))
    }

    override fun valueAtLong(p: Int): Long {
        return source.valueAtLong(computeIndex(p))
    }

    override fun valueAtDouble(p: Int): Double {
        return source.valueAtDouble(computeIndex(p))
    }

    private fun computeIndex(p: Int): Int {
        return when (dimensionsMultipliers.size) {
            0 -> 0
            1 -> selection[0].let { d -> if (d >= 0) d + p else p }
            else -> {
                val coords = dimensionsMultipliers.positionFromIndex(p)
                val adjusted = IntArray(coords.size) { i ->
                    val d = selection[i]
                    val v = coords[i]
                    if (d >= 0) {
                        d + v
                    } else {
                        v
                    }
                }
                sourceDimensions.indexFromPosition(adjusted)
            }
        }
    }

    fun replaceForUnder(replacement: APLValue): APLValue {
        val offset = IntArray(dimensions.size) { i ->
            val selectionValue = selection[i]
            if (selectionValue >= 0) {
                selectionValue
            } else {
                0
            }
        }
        return OverlayReplacementValue(source, dimensions, replacement, offset, pos)
    }

    class DropArrayValueMetadata(val sourceMetadata: APLValueMetadata, val selection: IntArray) : APLValueMetadata {
        override val labels by lazy { resolveLabels() }
        override val defaultValue get() = sourceMetadata.defaultValue

        private fun resolveLabels(): DimensionLabels? {
            return computeTakeDropLabels(sourceMetadata, selection) { labels, selected ->
                if (selected >= 0) {
                    labels.subList(selected, labels.size)
                } else {
                    labels.subList(0, labels.size + selected)
                }
            }
        }
    }
}

private inline fun computeTakeDropLabels(
    sourceMetadata: APLValueMetadata,
    selection: IntArray,
    computeSublist: (List<AxisLabel?>, Int) -> List<AxisLabel?>
): DimensionLabels? {
    val sourceLabels = sourceMetadata.labels ?: return null
    val resultLabels = ArrayList<List<AxisLabel?>?>()
    selection.forEachIndexed { index, selected ->
        val axisLabels = sourceLabels.labels[index]
        val res = if (axisLabels == null) {
            null
        } else {
            computeSublist(axisLabels, selected)
        }
        resultLabels.add(res)
    }
    return DimensionLabels(resultLabels)
}

class DropAPLFunction : APLFunctionDescriptor {
    class DropAPLFunctionImpl(pos: FunctionInstantiation) : APLFunction(pos) {
        override fun eval1Arg(context: RuntimeContext, a: APLValue, axis: APLValue?): APLValue {
            if (axis != null) {
                throwAPLException(AxisNotSupported(pos))
            }
            val sel = IntArray(a.dimensions.size) { i -> if (i == 0) 1 else 0 }
            return DropArrayValue(sel, a, pos)
        }

        override fun eval2Arg(context: RuntimeContext, a: APLValue, b: APLValue, axis: APLValue?): APLValue {
            val b0 = b.arrayify()
            val axisArray = if (axis == null) {
                val a0 = a.arrayify()
                val aDimensions = a0.dimensions
                if (aDimensions.size != 1) {
                    throwAPLException(InvalidDimensionsException("Left argument to drop must be a scalar or 1-dimensional array", pos))
                }
                val bDimensions = b0.dimensions
                if (aDimensions[0] > bDimensions.size) {
                    throwAPLException(InvalidDimensionsException("Size of A must be less than or equal to the rank of B", pos))
                }
                IntArray(bDimensions.size) { i ->
                    if (i < aDimensions[0]) {
                        a0.valueAtCoerceToInt(i, pos)
                    } else {
                        0
                    }
                }
            } else {
                val axisInt = axis.ensureNumber(pos).asInt()
                val a0 = a.unwrapDeferredValue()
                val a0Dimensions = a0.dimensions
                val argInteger = when {
                    a0Dimensions.size == 0 -> a0.ensureNumber(pos).asInt()
                    a0Dimensions.size == 1 && a0Dimensions[0] == 1 -> a0.valueAt(0).ensureNumber(pos).asInt()
                    else -> throwAPLException(APLIllegalArgumentException("When given an explicit axis, the left argument must be a single integer", pos))
                }
                val bDimensions = b0.dimensions
                ensureValidAxis(axisInt, bDimensions, pos)
                IntArray(bDimensions.size) { i -> if (i == axisInt) argInteger else 0 }
            }
            return DropArrayValue(axisArray, b0, pos)
        }

        override fun evalWithStructuralUnder1Arg(baseFn: APLFunction, context: RuntimeContext, a: APLValue, axis: APLValue?): APLValue {
            val underValue = eval1Arg(context, a, axis)
            if (underValue !is DropArrayValue) {
                throw IllegalStateException("Result is not of the correct type. Type = ${underValue::class}")
            }
            val updated = baseFn.eval1Arg(context, underValue, null)
            return underValue.replaceForUnder(updated)
        }

        override fun evalWithStructuralUnder2Arg(baseFn: APLFunction, context: RuntimeContext, a: APLValue, b: APLValue, axis: APLValue?): APLValue {
            val underValue = eval2Arg(context, a, b, axis)
            require(underValue is DropArrayValue) { "Result is not of the correct type. Type = ${underValue::class}" }
            val updated = baseFn.eval1Arg(context, underValue, null)
            return underValue.replaceForUnder(updated)
        }

        override val name1Arg get() = "drop"
        override val name2Arg get() = "drop"
    }

    override fun make(instantiation: FunctionInstantiation) = DropAPLFunctionImpl(instantiation)
}

class RotatedAPLValue private constructor(val source: APLValue, val axis: Int, val numShifts: Long) : APLArray() {
    private val axisActionFactors = AxisActionFactors(source.dimensions, axis)

    override val dimensions get() = source.dimensions
    override val specialisedType get() = source.specialisedType

    private fun computeValueAt(p: Int): Int {
        axisActionFactors.withFactors(p) { highVal, lowVal, axisCoord ->
            val coord = (axisCoord + numShifts).plusMod(dimensions[axis].toLong()).toInt()
            return (highVal * axisActionFactors.highValFactor) + (coord * axisActionFactors.multipliers[axis]) + lowVal
        }
    }

    override fun valueAt(p: Int) = source.valueAt(computeValueAt(p))
    override fun valueAtLong(p: Int) = source.valueAtLong(computeValueAt(p))
    override fun valueAtDouble(p: Int) = source.valueAtDouble(computeValueAt(p))

    override val metadata by lazy { maybeDefaultMetadata(source.metadata) { RotatedAPLValueMetadata(source, axis, numShifts) } }

    companion object {
        fun make(source: APLValue, axis: Int, numShifts: Long): APLValue {
            val dimensions = source.dimensions
            return if (dimensions.isEmpty() || numShifts % (dimensions[axis]) == 0L) {
                source
            } else {
                RotatedAPLValue(source, axis, numShifts)
            }
        }
    }

    private class RotatedAPLValueMetadata(source: APLValue, val axis: Int, val numShifts: Long) : APLValueMetadata {
        override val labels: DimensionLabels? by lazy { resolveLabels(source.metadata) }
        override val defaultValue = APLValueMetadata.DefaultMetadata.defaultValue
        private val sourceDimensions = source.dimensions

        private fun resolveLabels(sourceMetadata: APLValueMetadata): DimensionLabels? {
            val parent = sourceMetadata.labels ?: return null
            val parentList = parent.labels
            val newLabels = ArrayList<List<AxisLabel?>?>()
            parentList.forEachIndexed { i, axisLabels ->
                val newAxisLabels = when {
                    axisLabels == null -> null
                    i == axis -> (0 until sourceDimensions[axis]).map { i2 -> axisLabels[(i2 + numShifts).plusMod(sourceDimensions[axis].toLong()).toInt()] }
                    else -> axisLabels
                }
                newLabels.add(newAxisLabels)
            }
            return DimensionLabels(newLabels)
        }
    }
}

class MultiRotationRotatedAPLValue(
    val source: APLValue,
    val axis: Int,
    val selectionMultipliers: Dimensions.DimensionMultipliers,
    val selection: IntArray
) : APLArray() {
    override val dimensions = source.dimensions

    private val axisActionFactors = AxisActionFactors(source.dimensions, axis)
    private val sourceMultipliers = dimensions.multipliers()

    override val metadata by lazy { maybeDefaultMetadata(source.metadata) { MultiRotatedAPLValueMetadata(source, axis) } }

    override fun valueAt(p: Int): APLValue {
        val coords = sourceMultipliers.positionFromIndex(p)
        var curr = 0
        repeat(selectionMultipliers.size) { i ->
            val dimensionIndex = coords[if (i < axis) i else i + 1]
            curr += selectionMultipliers[i] * dimensionIndex
        }
        val numShifts = selection[curr]
        return axisActionFactors.withFactors(p) { highVal, lowVal, axisCoord ->
            val coord = (axisCoord + numShifts.toLong()).plusMod(dimensions[axis].toLong()).toInt()
            source.valueAt((highVal * axisActionFactors.highValFactor) + (coord * axisActionFactors.multipliers[axis]) + lowVal)
        }
    }

    private class MultiRotatedAPLValueMetadata(source: APLValue, val axis: Int) : APLValueMetadata {
        override val labels: DimensionLabels? by lazy { resolveLabels(source.metadata) }
        override val defaultValue = APLValueMetadata.DefaultMetadata.defaultValue

        private fun resolveLabels(sourceMetadata: APLValueMetadata): DimensionLabels? {
            val parent = sourceMetadata.labels ?: return null
            val newLabels = parent.labels.mapIndexed { i, axisLabels -> if (i == axis) null else axisLabels }
            return DimensionLabels(newLabels)
        }
    }
}

class InverseAPLValue private constructor(val source: APLValue, val axis: Int) : APLArray() {
    private val axisActionFactors = AxisActionFactors(source.dimensions, axis)

    override val dimensions = source.dimensions
    override val specialisedType get() = source.specialisedType

    override val metadata by lazy { maybeDefaultMetadata(source.metadata) { InverseAPLValueMetadata(this) } }

    override fun valueAt(p: Int): APLValue {
        return source.valueAt(destinationIndex(p))
    }

    override fun valueAtLong(p: Int): Long {
        return source.valueAtLong(destinationIndex(p))
    }

    override fun valueAtDouble(p: Int): Double {
        return source.valueAtDouble(destinationIndex(p))
    }

    private fun destinationIndex(p: Int): Int {
        return axisActionFactors.withFactors(p) { highVal, lowVal, axisCoord ->
            val coord = axisActionFactors.dimensions[axis] - axisCoord - 1
            (highVal * axisActionFactors.highValFactor) + (coord * axisActionFactors.multipliers[axis]) + lowVal
        }
    }

    companion object {
        fun make(source: APLValue, axis: Int): APLValue {
            val dimensions = source.dimensions
            return if (dimensions.isEmpty() || dimensions[axis] <= 1) {
                source
            } else {
                InverseAPLValue(source, axis)
            }
        }
    }

    private class InverseAPLValueMetadata(parent: InverseAPLValue) : APLValueMetadata {
        private val sourceMetadata: APLValueMetadata = parent.source.metadata
        private val axis = parent.axis

        override val labels: DimensionLabels? by lazy { resolveLabels(sourceMetadata) }
        override val defaultValue = APLValueMetadata.DefaultMetadata.defaultValue

        private fun resolveLabels(sourceMetadata: APLValueMetadata): DimensionLabels? {
            val parent = sourceMetadata.labels ?: return null
            val parentList = parent.labels
            val newLabels = ArrayList<List<AxisLabel?>?>()
            parentList.forEachIndexed { i, axisLabels ->
                val newAxisLabels = when {
                    axisLabels == null -> null
                    i == axis -> axisLabels.asReversed()
                    else -> axisLabels
                }
                newLabels.add(newAxisLabels)
            }
            return DimensionLabels(newLabels)
        }
    }
}

abstract class RotateFunction(pos: FunctionInstantiation) : APLFunction(pos) {
    override fun eval1Arg(context: RuntimeContext, a: APLValue, axis: APLValue?): APLValue {
        val axisInt = computeAxis(a, axis)
        return InverseAPLValue.make(a, axisInt)
    }

    override fun eval2Arg(context: RuntimeContext, a: APLValue, b: APLValue, axis: APLValue?): APLValue {
        return evalWithOptionalInverse(a, b, axis, false)
    }

    private fun evalWithOptionalInverse(a: APLValue, b: APLValue, axis: APLValue?, inverse: Boolean): APLValue {
        val bDimensions = b.dimensions
        val axisInt = computeAxis(b, axis)
        val a0 = a.arrayify()

        if (a0.size == 1) {
            val numShifts = a.valueAt(0).ensureNumber(pos).asLong(pos)
            return RotatedAPLValue.make(b, axisInt, numShifts * if (inverse) -1 else 1)
        } else {
            val a0Collapsed = a0.collapse()
            val aDimensions = a0Collapsed.dimensions
            if (aDimensions.size != bDimensions.size - 1) {
                throwAPLException(
                    InvalidDimensionsException(
                        "The rank of the rotation specifier must be one less than the rank of the array to be rotated",
                        pos))
            }
            repeat(aDimensions.size) { i ->
                if (aDimensions[i] != bDimensions[if (i < axisInt) i else i + 1]) {
                    throwAPLException(InvalidDimensionsException("Invalid dimension", pos))
                }
            }
            val a0Ints = if (inverse) {
                IntArray(a0Collapsed.size) { i -> -a0Collapsed.valueAtCoerceToInt(i, pos) }
            } else {
                a0Collapsed.toIntArray(pos)
            }
            return MultiRotationRotatedAPLValue(
                b,
                axisInt,
                a0Collapsed.dimensions.multipliers(),
                a0Ints)
        }
    }

    private fun computeAxis(v: APLValue, axis: APLValue?) =
        if (axis == null) defaultAxis(v) else axis.ensureNumber(pos).asInt(pos).also { ensureValidAxis(it, v.dimensions, pos) }

    override fun evalInverse1Arg(context: RuntimeContext, a: APLValue, axis: APLValue?) =
        eval1Arg(context, a, axis)

    override fun evalInverse2ArgB(context: RuntimeContext, a: APLValue, b: APLValue, axis: APLValue?): APLValue {
        return evalWithOptionalInverse(a, b, axis, true)
    }

    override fun evalWithStructuralUnder1Arg(baseFn: APLFunction, context: RuntimeContext, a: APLValue, axis: APLValue?): APLValue {
        return inversibleStructuralUnder1Arg(this, baseFn, context, a, axis)
    }

    override fun evalWithStructuralUnder2Arg(baseFn: APLFunction, context: RuntimeContext, a: APLValue, b: APLValue, axis: APLValue?): APLValue {
        return inversibleStructuralUnder2Arg(this, baseFn, context, a, b, axis)
    }

    abstract fun defaultAxis(value: APLValue): Int
}

