class TransposedAPLValue(axisList: IntArray, val b: APLValue, pos: Position, invert: Boolean) : APLArray() {
    override val dimensions: Dimensions
    private val multipliers: Dimensions.DimensionMultipliers
    private val bDimensions: Dimensions
    private val transposeAxis: IntArray
    override val specialisedType get() = b.specialisedType

    override val metadata by lazy { maybeDefaultMetadata(b.metadata) { TransposedAPLValueMetadata(this) } }

    init {
        val inverseTransposedAxis: IntArray
        if (invert) {
            transposeAxis = makeInverseTransposeIndex(axisList, pos)
            inverseTransposedAxis = axisList
        } else {
            transposeAxis = axisList
            inverseTransposedAxis = makeInverseTransposeIndex(axisList, pos)
        }

        bDimensions = b.dimensions
        dimensions = Dimensions(IntArray(bDimensions.size) { index -> bDimensions[inverseTransposedAxis[index]] })
        multipliers = dimensions.multipliers()
    }

    override fun valueAt(p: Int): APLValue {
        return b.valueAt(destinationIndex(p))
    }

    override fun valueAtLong(p: Int): Long {
        return b.valueAtLong(destinationIndex(p))
    }

    override fun valueAtDouble(p: Int): Double {
        return b.valueAtDouble(destinationIndex(p))
    }

    private fun destinationIndex(p: Int): Int {
        val c = multipliers.positionFromIndex(p)
        val newPos = IntArray(dimensions.size) { index -> c[transposeAxis[index]] }
        return bDimensions.indexFromPosition(newPos)
    }

    companion object {
        fun makeInverseTransposeIndex(transposeAxis: IntArray, pos: Position): IntArray {
            return IntArray(transposeAxis.size) { index ->
                var res = -1
                for (i in transposeAxis.indices) {
                    if (transposeAxis[i] == index) {
                        res = i
                        break
                    }
                }
                if (res == -1) {
                    throwAPLException(
                        InvalidDimensionsException(
                            "Not all axis represented in transpose definition: ${ArrayUtils.toString(transposeAxis)}",
                            pos))
                }
                res
            }
        }
    }

    class TransposedAPLValueMetadata(parent: TransposedAPLValue) : APLValueMetadata {
        private val sourceMetadata = parent.b.metadata
        private val transposeAxis = parent.transposeAxis

        override val labels by lazy { resolveLabels() }
        override val defaultValue = APLValueMetadata.DefaultMetadata.defaultValue

        private fun resolveLabels(): DimensionLabels? {
            val parent = sourceMetadata.labels ?: return null
            val parentList = parent.labels
            val newLabels = ArrayList<List<AxisLabel?>?>()
            for (origAxis in transposeAxis) {
                newLabels.add(parentList[origAxis])
            }
            return DimensionLabels(newLabels)
        }
    }
}

class TransposeFunction : APLFunctionDescriptor {
    class TransposeFunctionImpl(pos: FunctionInstantiation) : APLFunction(pos) {
        override fun eval1Arg(context: RuntimeContext, a: APLValue, axis: APLValue?): APLValue {
            return if (a.isScalar()) {
                a
            } else {
                val size = a.dimensions.size
                val axisArg = IntArray(size) { i -> size - i - 1 }
                TransposedAPLValue(axisArg, a, pos, false)
            }
        }

        override fun eval2Arg(context: RuntimeContext, a: APLValue, b: APLValue, axis: APLValue?): APLValue {
            return computeTranspose(a, b, false)
        }

        override fun evalInverse1Arg(context: RuntimeContext, a: APLValue, axis: APLValue?): APLValue {
            ensureAxisNull(axis)
            return eval1Arg(context, a, axis)
        }

        override fun evalInverse2ArgB(context: RuntimeContext, a: APLValue, b: APLValue, axis: APLValue?): APLValue {
            ensureAxisNull(axis)
            return computeTranspose(a, b, true)
        }

        override fun evalWithStructuralUnder1Arg(baseFn: APLFunction, context: RuntimeContext, a: APLValue, axis: APLValue?): APLValue {
            return inversibleStructuralUnder1Arg(this, baseFn, context, a, axis)
        }

        override fun evalWithStructuralUnder2Arg(baseFn: APLFunction, context: RuntimeContext, a: APLValue, b: APLValue, axis: APLValue?): APLValue {
            return inversibleStructuralUnder2Arg(this, baseFn, context, a, b, axis)
        }

        private fun computeTranspose(a: APLValue, b: APLValue, invert: Boolean): APLValue {
            val a1 = a.arrayify()
            val aDimensions = a1.dimensions
            val bDimensions = b.dimensions
            if (aDimensions.size != 1 || aDimensions[0] != bDimensions.size) {
                throwAPLException(InvalidDimensionsException("Transpose arguments have wrong dimensions", pos))
            }

            if (b.isScalar()) {
                if (aDimensions[0] == 0) {
                    return b
                } else {
                    throwAPLException(
                        InvalidDimensionsException(
                            "Transpose of scalar values requires empty left argument",
                            pos))
                }
            }

            val transposeAxis = IntArray(aDimensions[0]) { index -> a1.valueAtCoerceToInt(index, pos) }
            return TransposedAPLValue(transposeAxis, b, pos, invert)
        }

        override val name1Arg get() = "transpose"
        override val name2Arg get() = "transpose"
    }

    override fun make(instantiation: FunctionInstantiation) = TransposeFunctionImpl(instantiation)
}

object MemberResultValueImpls {
    class MemberResultValueHashGeneric(val a: APLValue, val b: APLValue) : APLArray() {
        override val dimensions = a.dimensions
        override val specialisedType get() = ArrayMemberType.BOOLEAN

        private val foundElements = HashSet<APLValueKey>()
        private val foundElementsLock: MPLock = MPLock()
        private var currPosition = 0
        private val numElements = b.size

        override fun valueAtLong(p: Int) = if (opLong(p)) 1L else 0L
        override fun valueAt(p: Int) = opLong(p).makeAPLNumberAsBoolean()

        private fun opLong(p: Int): Boolean {
            val target = a.valueAt(p)
            val targetKey = target.makeNonTypeQualifiedKey()
            foundElementsLock.withLocked {
                if (foundElements.contains(targetKey)) {
                    return true
                }
                while (currPosition < numElements) {
                    val v = b.valueAt(currPosition++)
                    foundElements.add(v.makeNonTypeQualifiedKey())
                    if (target.compareEqualsTotalOrdering(v, typeDiscrimination = false)) {
                        return true
                    }
                }
            }
            return false
        }
    }
}

class MemberFunction : APLFunctionDescriptor {
    class MemberFunctionImpl(pos: FunctionInstantiation) : NoAxisAPLFunction(pos) {
        override fun eval1Arg(context: RuntimeContext, a: APLValue, axis: APLValue?): APLValue {
            return if (a.specialisedType.isAtom) {
                if (a.dimensions.size <= 1) {
                    a
                } else {
                    ResizedArrayImpls.makeResizedArray(dimensionsOfSize(a.size), a)
                }
            } else {
                val limit = axis?.let { a -> a.ensureNumber(pos).asInt(pos) }
                if (limit != null) {
                    if (limit < 0) {
                        throwAPLException(APLIllegalArgumentException("Negative enlist limit: ${limit}"))
                    }
                    if (limit == 0) {
                        return a
                    }
                }
                val result = ArrayList<APLValue>()
                val specialisedType = enlistInner(result, a, 0, limit)
                return APLArrayList(dimensionsOfSize(result.size), result, specialisedType)
            }
        }

        private fun enlistInner(result: MutableList<APLValue>, value: APLValue, level: Int, limit: Int?): ArrayMemberType {
            val v = value.unwrapDeferredValue()
            return if (v.isAtomic || limit != null && level > limit) {
                result.add(v)
                v.specialisedTypeAsMember
            } else {
                var type = ArrayMemberType.ALL
                v.iterateMembers { inner ->
                    val newType = enlistInner(result, inner, level + 1, limit)
                    type = type.intersection(newType)
                }
                type
            }
        }

        override fun eval2Arg(context: RuntimeContext, a: APLValue, b: APLValue): APLValue {
            return if (a.isScalar()) {
                linearSearch(a.disclose(), b)
            } else {
                MemberResultValueImpls.MemberResultValueHashGeneric(a, b)
            }
        }

        private fun linearSearch(a: APLValue, b: APLValue): APLValue {
            val bst = b.specialisedType
            return when {
                (a is APLLong && bst.isLong) -> linearSearchLong(a.value, b)
                else -> linearSearchGeneric(a, b)
            }
        }

        private fun linearSearchGeneric(a: APLValue, b: APLValue): APLValue {
            b.iterateMembers { v ->
                if (a.compareEqualsTotalOrdering(v, pos = pos, typeDiscrimination = false)) {
                    return APLLONG_1
                }
            }
            return APLLONG_0
        }

        private fun linearSearchLong(a: Long, b: APLValue): APLValue {
            repeat(b.size) { i ->
                try {
                    if (b.valueAtLong(i) == a) {
                        return APLLONG_1
                    }
                } catch (e: LongExpressionOverflow) {
                    if (e.result.rangeInLong() && e.result.toLong() == a) {
                        return APLLONG_1
                    }
                }
            }
            return APLLONG_0
        }

        override val name1Arg get() = "enlist"
        override val name2Arg get() = "member"
    }

    override fun make(instantiation: FunctionInstantiation) = MemberFunctionImpl(instantiation)
}

class FindResultValue(val context: RuntimeContext, val a: APLValue, val b: APLValue) : APLArray() {
    override val dimensions = b.dimensions
    override val specialisedType get() = ArrayMemberType.BOOLEAN

    private val aDimensions = a.dimensions
    private val aMultipliers = aDimensions.multipliers()
    private val bMultipliers = dimensions.multipliers()

    override fun valueAtLong(p: Int): Long {
        return if (opLong(p)) 1 else 0
    }

    override fun valueAt(p: Int): APLValue {
        return opLong(p).makeAPLNumberAsBoolean()
    }

    private fun opLong(p: Int): Boolean {
        val dimensionsDiff = dimensions.size - aDimensions.size
        val coord = bMultipliers.positionFromIndex(p)

        if (aDimensions.size > dimensions.size) {
            return false
        }

        aDimensions.forEachIndexed { i, v ->
            if (coord[dimensionsDiff + i] > dimensions[dimensionsDiff + i] - v) {
                return false
            }
        }

        fun processOneLevel(level: Int, aCurr: Int, bCurr: Int): Boolean {
            if (level == aDimensions.size) {
                return a.valueAt(aCurr).compareEqualsTotalOrdering(b.valueAt(bCurr), typeDiscrimination = false)
            } else {
                val axis = dimensionsDiff + level
                val aStride = aMultipliers[level]
                val bStride = bMultipliers[axis]
                val length = aDimensions[level]
                for (i in 0 until length) {
                    val ap = aCurr + i * aStride
                    val bp = bCurr + i * bStride
                    if (!processOneLevel(level + 1, ap, bp)) {
                        return false
                    }
                }
                return true
            }
        }

        return processOneLevel(0, 0, p)
    }
}

class FindFunction : APLFunctionDescriptor {
    class FindFunctionImpl(pos: FunctionInstantiation) : NoAxisAPLFunction(pos) {
        override fun eval2Arg(context: RuntimeContext, a: APLValue, b: APLValue): APLValue {
            return if (b.dimensions.size == 0) {
                if (a.compareEqualsTotalOrdering(b, pos)) APLLONG_1 else APLLONG_0
            } else {
                FindResultValue(context, a, b)
            }
        }

        override val name2Arg get() = "find"
    }

    override fun make(instantiation: FunctionInstantiation) = FindFunctionImpl(instantiation)
}

class SelectElementsValue(selectIndexes: IntArray, val b: APLValue, val axis: Int) : APLArray() {
    private val bDimensions = b.dimensions
    override val dimensions: Dimensions
    override val specialisedType get() = b.specialisedType

    private val axisActionFactors: AxisActionFactors
    private val highMultiplier: Int
    private val bStride: Int
    private val aIndex: IntArray

    init {
        val sizeAlongAxis = selectIndexes.reduceWithInitial(0) { a, b -> a + b }
        dimensions = Dimensions(IntArray(bDimensions.size) { i ->
            if (i == axis) {
                sizeAlongAxis
            } else {
                bDimensions[i]
            }
        })

        val m = bDimensions.multipliers()
        highMultiplier = if (axis == 0) bDimensions.size else m[axis - 1]
        bStride = m[axis]

        aIndex = IntArray(sizeAlongAxis)
        var aIndexPos = 0
        var bIndexPos = 0
        for (dimensionsIndex in selectIndexes) {
            for (i2 in 0 until dimensionsIndex) {
                aIndex[aIndexPos++] = bIndexPos
            }
            bIndexPos++
        }

        axisActionFactors = AxisActionFactors(dimensions, axis)
    }

    private inline fun valueAtImpl(p: Int, fn: (Int) -> Nothing): Nothing {
        axisActionFactors.withFactors(p) { high, low, axisCoord ->
            val bIndexPos = aIndex[axisCoord]
            val resultPos = high * highMultiplier + bIndexPos * bStride + low
            fn(resultPos)
        }
    }

    override fun valueAt(p: Int): APLValue {
        valueAtImpl(p) { resultPos ->
            return b.valueAt(resultPos)
        }
    }

    override fun valueAtLong(p: Int): Long {
        valueAtImpl(p) { resultPos ->
            return b.valueAtLong(resultPos)
        }
    }

    override fun valueAtDouble(p: Int): Double {
        valueAtImpl(p) { resultPos ->
            return b.valueAtDouble(resultPos)
        }
    }

    override val metadata by lazy { maybeDefaultMetadata(b.metadata) { m -> SelectElementsValueMetadata(m, selectIndexes, axis) } }

    private class SelectElementsValueMetadata(val sourceMetadata: APLValueMetadata, val selectIndexes: IntArray, val axis: Int) : APLValueMetadata {
        override val labels by lazy { resolveLabels() }
        override val defaultValue get() = sourceMetadata.defaultValue

        private fun resolveLabels(): DimensionLabels? {
            val origLabels = sourceMetadata.labels ?: return null
            val res = ArrayList<List<AxisLabel?>?>()
            origLabels.labels.forEachIndexed { axisIndex, axisLabels ->
                val a = if (axisLabels == null) {
                    null
                } else {
                    val inner = ArrayList<AxisLabel?>()
                    if (axisIndex != axis) {
                        inner.addAll(axisLabels)
                    } else {
                        selectIndexes.forEachIndexed { i, n ->
                            if (n == 1) {
                                inner.add(axisLabels[i])
                            } else if (n > 1) {
                                repeat(n) {
                                    inner.add(null)
                                }
                            }
                        }
                    }
                    inner
                }
                res.add(a)
            }
            return DimensionLabels(res)
        }
    }
}

@Suppress("IfThenToElvis")
abstract class SelectElementsFunctionImpl(pos: FunctionInstantiation) : APLFunction(pos) {
    override fun eval2Arg(context: RuntimeContext, a: APLValue, b: APLValue, axis: APLValue?): APLValue {
        val bFixed = b.arrayify()
        val aDimensions = a.dimensions
        val bDimensions = bFixed.dimensions
        val axisInt = if (axis == null) defaultAxis(bFixed) else axis.ensureNumber(pos).asInt(pos)
        ensureValidAxis(axisInt, bDimensions, pos)
        if (!(aDimensions.size == 0 || (aDimensions.size == 1 && aDimensions[0] == bDimensions[axisInt]))) {
            throwAPLException(
                InvalidDimensionsException(
                    "A must be a single-dimensional array of the same size as the dimension of B along the selected axis.",
                    pos))
        }
        val selectIndexes = if (a.isScalar()) {
            a.ensureNumber(pos).asInt(pos).let { v ->
                if (v < 0) {
                    throwAPLException(IncompatibleTypeException("Selection index is negative", pos))
                }
                IntArray(bDimensions[axisInt]) { v }
            }
        } else {
            a.toIntArray(pos).onEach { v ->
                if (v < 0) {
                    throwAPLException(IncompatibleTypeException("Selection index is negative", pos))
                }
            }
        }
        return SelectElementsValue(selectIndexes, bFixed, axisInt)
    }

    abstract fun defaultAxis(value: APLValue): Int
}

