actual fun BigInt.Companion.of(value: Short): BigInt {
    @Suppress("UNUSED_VARIABLE")
    val a = value.toInt()
    return makeFromJs(js("BigInt(a)"))
}

actual fun BigInt.Companion.of(value: Int): BigInt {
