actual fun BigInt.rangeInLong(): Boolean {
    return this >= BigIntConstants.LONG_MIN_VALUE && this <= BigIntConstants.LONG_MAX_VALUE
}

actual fun BigInt.rangeInInt(): Boolean {
    return this >= BigIntConstants.INT_MIN_VALUE && this <= BigIntConstants.INT_MAX_VALUE
}

actual fun BigInt.bitLength(): Long {
    @Suppress("UNUSED_VARIABLE")
    val a = this.inner
//    val res = js("(function(a0) { if(a0==0||a0==-1) { return 0; } if(a0<0) { return (BigInt(-1)-a0).toString(2).length; } else { return a0.toString(2).length; } })(a)") as Double
    val res = js("(function(a0){var n; if(a0<0) {n=BigInt(-1)-a0;} else {n=a0;} if(n==0) {return 0;} return n.toString(2).length;})(a)") as Double
    return res.toLong()
}

actual fun BigInt.popcnt(): Long {
    val a = this.inner
    val s = if (a < 0) {
        js("(BigInt(-1) - a).toString(2)") as String
    } else {
        js("a.toString(2)") as String
    }
    return s.count { ch -> ch == '1' }.toLong()
}

actual fun BigInt.isqrt(): BigInt {
    if (this < 0) {
        throw ArithmeticException("Argument is negative")
    }

    if (this < 2) {
        return this
    }

    @Suppress("UNUSED_VARIABLE")
    val a = this.inner
    val bitLength = (js("a.toString(2)") as String).length
    val shift = bitLength / 2
    var xk = (this shr shift) shl (shift / 2)

    while (true) {
        val xk1 = ((this / xk) + xk) shr 1
        if (xk1 >= xk) {
            return xk
        }
        xk = xk1
    }
}
