package com.dhsdevelopments.kap

import kotlin.contracts.ExperimentalContracts
import kotlin.contracts.InvocationKind
import kotlin.contracts.contract
import kotlin.math.min

open class MPFileException(message: String, cause: Throwable? = null) : Exception(message, cause)
open class MPFileNotFoundException(message: String, cause: Throwable? = null) : MPFileException(message, cause)

@OptIn(ExperimentalContracts::class)
inline fun <T> transformMPFileException(pos: Position?, fn: () -> T): T {
    contract { callsInPlace(fn, InvocationKind.EXACTLY_ONCE) }
    try {
        return fn()
    } catch (e: MPFileException) {
        throwAPLException(APLEvalException("File exception: ${e.message}", pos))
    }
}

interface NativeCloseable {
    fun close()
}

@OptIn(ExperimentalContracts::class)
inline fun <T : NativeCloseable, R> T.use(fn: (T) -> R): R {
    contract { callsInPlace(fn, InvocationKind.EXACTLY_ONCE) }
    try {
        return fn(this)
    } finally {
        close()
    }
}

interface ByteProvider : NativeCloseable {
    fun sourceName(): String? = null
    fun readByte(): Byte?

    fun readBlock(buffer: ByteArray, start: Int? = null, length: Int? = null): Int {
        val startPos = start ?: 0
        val n = length ?: (buffer.size - startPos)
        var i = 0
        while (i < n) {
            val value = readByte() ?: break
            buffer[startPos + i] = value
            i++
        }
        return i
    }

    fun readBlockComplete(buffer: ByteArray, start: Int? = null, length: Int? = null): Boolean {
        val startPos = start ?: 0
        val n = length ?: (buffer.size - startPos)
        var i = 0
        while (i < n) {
            val res = readBlock(buffer, startPos + i, n - i)
            if (res == 0) {
                return false
            }
            i += res
        }
        return true
    }

    fun readNetworkOrderInt(): Int? {
        val buf = ByteArray(4)
        if (!readBlockComplete(buf)) {
            return null
        }
        return ((buf[0].toInt() and 0xFF) shl 24) or
                ((buf[1].toInt() and 0xFF) shl 16) or
                ((buf[2].toInt() and 0xFF) shl 8) or
                (buf[3].toInt() and 0xFF)
    }
}

class ByteArrayByteProvider(val content: ByteArray, val name: String? = null) : ByteProvider {
    override fun sourceName() = name

    var pos = 0

    override fun readByte(): Byte? {
        return if (pos >= content.size) null else content[pos++]
    }

    override fun close() {
    }
}

class BufferedByteProvider(val src: ByteProvider, bufSize: Int = 16384) : ByteProvider {
    private val srcBuf = ByteArray(bufSize)
    private var pos = 0
    private var bufEnd = 0

    override fun readByte(): Byte? {
        fillBuffer()
        if (bufEnd == -1) {
            return null
        }
        return srcBuf[pos++]
    }

    override fun readBlock(buffer: ByteArray, start: Int?, length: Int?): Int {
        val start0 = start ?: 0
        val end = if (length == null) buffer.size - start0 else start0 + length
        var curr = start0
        while (curr < end) {
            fillBuffer()
            if (pos == -1) {
                break
            }
            val n = min(bufEnd - pos, end - curr)
            srcBuf.copyInto(buffer, curr, pos, curr + n)
            curr += n
            pos += n
        }
        return curr - start0
    }

    override fun close() {
        src.close()
    }

    private fun fillBuffer() {
        if (bufEnd == -1) {
            return
        }
        require(pos <= bufEnd) { "pos > bufEnd: pos=${pos}, bufEnd=${bufEnd}" }
        if (pos == bufEnd) {
            val n = src.readBlock(srcBuf, 0, srcBuf.size)
            bufEnd = if (n == 0) -1 else n
            require(bufEnd != 0) { "got 0 from readBlock" }
            pos = 0
        }
    }
}

interface CharacterProvider : NativeCloseable {
    fun sourceName(): String? = null
    fun nextCodepoint(): Int?

    fun nextLine(): String? {
        val buf = StringBuilder()
        while (true) {
            val ch = nextCodepoint()
            if (ch == null) {
                val s = buf.toString()
                return s.ifEmpty { null }
            } else if (ch == '\n'.code) {
                return buf.toString()
            }
            buf.addCodepoint(ch)
        }
    }

    fun lines() = generateSequence { nextLine() }
}

interface ByteConsumer : NativeCloseable {
    fun writeByte(value: Byte)

    fun writeBlock(buffer: ByteArray, offset: Int = 0, length: Int? = null) {
        for (i in offset until offset + (length ?: (buffer.size - offset))) {
            writeByte(buffer[i])
        }
    }

    fun writeNetworkOrderInt(value: Int) {
        val buf = ByteArray(4)
        buf[0] = ((value shr 24) and 0xFF).toByte()
        buf[1] = ((value shr 16) and 0xFF).toByte()
        buf[2] = ((value shr 8) and 0xFF).toByte()
        buf[3] = (value and 0xFF).toByte()
        writeBlock(buf)
    }

    fun flush() = Unit
}

class ByteArrayByteConsumer : ByteConsumer {
    private var content = ByteArray(1024)
    private var size = 0

    override fun writeByte(value: Byte) {
        if (size >= content.size) {
            content = content.copyOf((content.size * 3) / 2)
        }
        content[size++] = value
    }

    override fun close() {
    }

    fun content() = content.copyOf(size)
}

interface CharacterConsumer : NativeCloseable {
    fun writeChar(ch: Int)

    fun writeString(s: String) {
        s.asCodepointList().forEach(::writeChar)
    }
}

class PushBackCharacterProvider(val sourceLocation: SourceLocation) : CharacterProvider {
    private class CharWithPosition(val character: Int, val line: Int, val col: Int)

    private val source = sourceLocation.open()

    private val pushBackList = ArrayList<CharWithPosition>()
    private val pushBackHistory = ArrayDeque<CharWithPosition>()

    private var line = 0
    private var col = 0

    fun nextCodepointWithPos() = Pair(nextCodepoint(), pos())

    override fun nextCodepoint(): Int? {
        val ch = if (pushBackList.isNotEmpty()) {
            pushBackList.removeLast()
        } else {
            val ch = source.nextCodepoint() ?: return null
            val chWithPos = CharWithPosition(ch, line, col)
            pushBackHistory.addLast(chWithPos)
            // Keep the pushback list at 5 elements at most
            while (pushBackHistory.size > 5) {
                pushBackHistory.removeFirst()
            }
            chWithPos
        }
        if (ch.character == '\n'.code) {
            line++
            col = 0
        } else {
            col++
        }
        return ch.character
    }

    fun pushBack() {
        val ch = pushBackHistory.removeLast()
        pushBackList.add(ch)
        line = ch.line
        col = ch.col
    }

    fun pos() = Position(sourceLocation, line, col)

    override fun close() {
        source.close()
    }

    override fun sourceName() = source.sourceName()
}

class ByteToCharacterProvider(val source: ByteProvider) : CharacterProvider {
    override fun sourceName(): String? = source.sourceName()

    private var pushbackChar: Byte? = null

    override fun nextCodepoint(): Int? {
        var utfCodepoint = 0
        var utfBytesSeen = 0
        var utfBytesNeeded = 0
        var utfLowerBoundary = 0x80
        var utfUpperBoundary = 0xBF

        while (true) {
            val nextByte = source.readByte()
                ?: if (utfBytesNeeded != 0) {
                    throw MPFileException("Truncated UTF-8 stream")
                } else {
                    return null
                }
            val a = nextByte.toInt() and 0xFF
            if (utfBytesNeeded == 0) {
                when {
                    a <= 0x7F -> {
                        return a
                    }
                    a in (0xC2..0xDF) -> {
                        utfBytesNeeded = 1
                        utfCodepoint = (a and 0x1F)
                    }
                    a in (0xE0..0xEF) -> {
                        if (a == 0xE0) utfLowerBoundary = 0xA0
                        else if (a == 0xED) utfUpperBoundary = 0x9F
                        utfBytesNeeded = 2
                        utfCodepoint = (a and 0xF)
                    }
                    a in (0xF0..0xF4) -> {
                        if (a == 0xF0) utfLowerBoundary = 0x90
                        else if (a == 0xF4) utfUpperBoundary = 0x8F
                        utfBytesNeeded = 3
                        utfCodepoint = (a and 0x7)
                    }
                    else -> throw MPFileException("Unexpected value in UTF-8 stream")
                }
            } else {
                if (a < utfLowerBoundary || a > utfUpperBoundary) {
                    pushbackChar = nextByte
                    throw MPFileException("Invalid UTF-8 bytes")
                }
                utfLowerBoundary = 0x80
                utfUpperBoundary = 0xBF
                utfCodepoint = (utfCodepoint shl 6) or (a and 0x3F)
                utfBytesSeen++
                if (utfBytesSeen == utfBytesNeeded) {
                    return utfCodepoint
                }
            }
        }
    }

    override fun close() {
        source.close()
    }
}

class CharacterToByteConsumer(val dest: ByteConsumer) : CharacterConsumer {
    override fun writeChar(ch: Int) {
        dest.writeBlock(charToString(ch).encodeToByteArray())
    }

    override fun writeString(s: String) {
        dest.writeBlock(s.encodeToByteArray())
    }

    override fun close() {
        dest.close()
    }
}

class AppendableCharConsumer(val dest: Appendable) : CharacterConsumer {
    override fun writeChar(ch: Int) {
        dest.append(charToString(ch))
    }

    override fun writeString(s: String) {
        dest.append(s)
    }

    override fun close() {}

}

expect fun makeStringCharacterProvider(s: String): CharacterProvider

interface KeyboardInput : NativeCloseable {
    fun readString(prompt: String): String?
    override fun close() {}
}

expect fun makeKeyboardInput(engine: Engine): KeyboardInput?

